<style lang="scss" scoped>

</style>
<template>
  <page-box>
        <div class="ui-search-box" slot="search-form">
            <el-form class="ui-search-form"  label-width="120px">
                <el-form-item label="权限id">
                    <el-input size="mini" class="ui-item-reset" clearable v-model="params.permissionId"></el-input>
                </el-form-item>
                <el-form-item label="名称">
                    <el-input size="mini" class="ui-item-reset" clearable v-model="params.name"></el-input>
                </el-form-item>
                <el-form-item label="路径url">
                    <el-input size="mini" class="ui-item-reset" clearable v-model="params.url"></el-input>
                </el-form-item>
                <el-form-item label="接口标识">
                    <el-input size="mini" class="ui-item-reset" clearable v-model="params.perms"></el-input>
                </el-form-item>
                <el-form-item label="每页数据">
                    <el-select size="mini" class="ui-item-reset" clearable v-model="params.pageSize">
                        <el-option label="5" :value="5"></el-option>
                        <el-option label="10" :value="10"></el-option>
                        <el-option label="20" :value="20"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <div class="ui-search-btus">
                <el-button type="primary" size="mini" @click="getData">搜索</el-button>
                <!-- <el-button size="mini">重置</el-button> -->
            </div>
        </div>
        <div class="ui-list ui-box" slot="list-box">
            <div class="box-header ui-list-header">
                <div>权限列表</div>
                <div>
                    <el-button type="primary" size="mini" @click="exportExcel">导出</el-button>
                    <el-button type="primary" size="mini" @click="open">新增</el-button>
                </div>
            </div>
            <el-table v-loading="tabLoading" :data="dataList" @selection-change="handleSelectionChange" :cell-style="cellStyle" :header-cell-style="rowClass">
                <el-table-column type="selection" width="55"></el-table-column>
                <el-table-column prop="permissionId" label="权限id"></el-table-column>
                <el-table-column prop="name" label="名称"></el-table-column>
                <el-table-column prop="url" label="路径"></el-table-column>
                <el-table-column prop="status" label="状态">
                    <template slot-scope="scope">
                        <el-button type="success" icon="el-icon-check" circle size="mini" v-if="scope.row.status==1"></el-button>
                        <el-button type="danger" icon="el-icon-close" circle v-else size="mini"></el-button>
                    </template>
                </el-table-column>
                <el-table-column prop="description" label="描述"></el-table-column>
                <el-table-column prop="createTime" label="创建时间" show-overflow-tooltip></el-table-column>
                <el-table-column prop="updateTime" label="更新时间" show-overflow-tooltip></el-table-column>
                <el-table-column label="操作">
                    <template slot-scope="scope">
                            <el-button size="mini" @click="checkDetail(scope.row)">详情</el-button>
                            <el-button size="mini" type="danger" @click="deleteItem(scope.row)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <el-pagination
                class="ui-pagination"
                @current-change="changPageNum"
                layout="total, prev, pager, next"
                :current-page="this.params.pageNum"
                :page-size="this.params.pageSize"
                :total="total">
            </el-pagination>
            <add-item v-bind:permsItem.sync='permsItem' @submit="addItem" ref="addPerm"></add-item>
        </div>
  </page-box>
</template>
<script>
import pageBox from '@/components/pageBox'
import addItem from '@/components/permissions/addItem.vue'
export default {
    name: 'permissionsList',
    components:{
        pageBox,addItem
    },
    data(){
        return{
            dataList: [],
            exportList: [],
            test:'1',
            permsItem:{
                name: '',
                parentId: '0',
                url: '',
                perms: '',
                status: '1',
                description: '',
                type: '0',
                orderNum: '0',
            },
            tabLoading: false,
            params:{
                permissionId:'',
                name:'',
                url: '',
                perms: '',
                pageNum: 1,
                pageSize: 20,
            },
            total:0,
        }
    },
    created(){
        this.getData()
    },
    methods:{
        getData(){
             post('/pms_api/permission/list',this.params).then(res => {
                this.dataList=res.rows
                this.total=res.total
                this.tabLoading=false
            }).catch(err => {
                console.log(err)
                this.tabLoading=false
            })
        },
        addItem(){
            this.tabLoading=true
            this.permsItem.type='0'
            if(this.permsItem.parentId!=0){
                this.permsItem.type='2'
            }
            post('/pms_api/permission/add',this.permsItem).then(res => {
                this.$refs.addPerm.addDialogVisible=false;
                this.getData();
            }).catch(err => {
                console.log(err)
            })  
        },
        changPageNum(val){
            this.params.pageNum=val
            this.getData()
        },
        open(){
            // this.addDialogVisible=!this.addDialogVisible
            this.$refs.addPerm.openWindow('新增路由')
        },
        checkDetail(item){
            this.$router.push({
                path: '/permissionsDetail',
                query:{
                    title: item.name,
                    id: item.permissionId
                }
            })
        },
        deleteItem(item){
            post('/pms_api/permission/delete',{'permissionId':item.permissionId}).then(res => {
                this.getData()
            }).catch(err => {
                console.log(err)
            })
        },
        handleSelectionChange(val){
            this.exportList=val
        },
        exportExcel(){
           // 暂时无法完成 xlsx
           this.$message.warning(`抱歉，无法导出`)
        },
        //表格居中
        cellStyle({row, colum, rowIndex, columIndex}){
            return "text-align: center";
        },
        rowClass({row, rowIndex}){
            return "text-align: center";
        }
    }
}
</script>





