<style lang="scss" scope>
.item_reset{
    width: 180px;
}
</style>
<template>
    <el-dialog :title="title" :visible.sync="addDialogVisible" width="40%">
        <div class="ui-box leave_dialog">
            <el-form :model="permsItem" label-width="80px" :rules="formRule" ref="formPerms">
                <el-form-item label="名称" prop="name">
                    <el-input v-model="permsItem.name" size="mini" class="item_reset"></el-input>
                </el-form-item>
                <el-form-item label="父级路由">
                    <el-select placeholder="父级路由" size="mini" class="item_reset" v-model="permsItem.parentId" clearable>
                        <el-option label="无" value="0"></el-option>
                        <el-option :label="item.name" :value="item.id" v-for="item in parentRoute" :key="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="api">
                    <el-input v-model="permsItem.perms" class="item_reset" size="mini"></el-input>
                </el-form-item>
                <!-- <el-form-item label="层级" v-if="permsItem.parentId!='0'">
                    <el-select placeholder="层级" size="mini" class="item_reset" v-model="permsItem.type" clearable>
                        <el-option label="父路由" :value="1"></el-option>
                        <el-option label="子路由" :value="2"></el-option>
                    </el-select>
                </el-form-item> -->
                <el-form-item label="排序">
                    <el-input v-model="permsItem.orderNum" class="item_reset" size="mini"></el-input>
                </el-form-item>
                <el-form-item label="url路径" prop="url">
                    <el-input v-model="permsItem.url" class="item_reset" size="mini"></el-input>
                </el-form-item>
                <el-form-item label="状态">
                    <el-select placeholder="请选择" class="item_reset" size="mini" v-model="permsItem.status">
                        <el-option label="禁用" value="2"></el-option>
                        <el-option label="启用" value="1"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="描述">
                    <el-input v-model="permsItem.description" class="item_reset" size="mini"></el-input>
                </el-form-item>
            </el-form>
        </div>
        <span slot="footer" class="dialog-footer">
            <el-button @click="addDialogVisible = false" size="mini">取 消</el-button>
            <el-button type="primary" @click="submit" size="mini">确 定</el-button>
        </span>
    </el-dialog>
</template>
<script>
export default {
    name: 'addItem',
    props: {
        permsItem:{}
    },
    data(){
        return{
            formRule:{
                name: [{required:true,message:'请输入名称',trigger: 'blur'}],
                url: [{required:true,message:'请输入路径',trigger: 'blur'}],
            },
            addDialogVisible: false,
            title: '',
            parentRoute:[],
        }
    },
    mounted(){
        this.initParentRoute()
    },
    methods:{
        submit(){
            this.$refs['formPerms'].validate((val) =>{
                if(val){
                    this.$emit('submit')
                }
            })
        },
        openWindow(value){
            // console.log('title',value);
            this.title=value;
            this.addDialogVisible=true;
        },
        initParentRoute(){
            this.parentRoute=this.$getGlobal.getItem('routers')

        }
    }
}
</script>




